import React, { Component } from 'react';
import { Row, Col, Form, Checkbox, Input, Select, message } from 'antd';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import FilterPathEducate from '../../../components/common/input/FilterPathEducate/index';
import { SelectAjax, DatePickerString } from '../../../components/common/index';
import { Btn, BtnClear } from '../../../components/common/button';

// const { RangePicker } = DatePicker;
const { Option } = Select;
@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  getSemester = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Học kỳ 1'
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getLop = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Lớp A'
        },
        {
          id: '2',
          label: 'Lớp B'
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getLanthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1'
        },
        {
          id: '2',
          label: '2'
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getLoaithu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Thu SV nhập học'
        },
        {
          id: '2',
          label: 'Thu khác'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getHinhthucthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Hình thức A'
        },
        {
          id: '2',
          label: 'Hình thức B'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getNamhoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '2018-2019'
        },
        {
          id: '2',
          label: '2019-2020'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getDot = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1'
        },
        {
          id: '2',
          label: '2'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getDoituonghocphi = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Ngoài ngân sách'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getKhoahoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Khóa 2018'
        },
        {
          id: '2',
          label: 'Khóa 2019'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  exportExcel = () => {
    message.success('Demo disable when table data is empty');
  }

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  render() {
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const phieuhuy = getFieldValue('phieuhuy');
    const phieuthu = getFieldValue('phieuthu');
    const phieuchi = getFieldValue('phieuchi');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <FilterPathEducate form={this.props.form} classes />
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Năm học">
              {getFieldDecorator('namhoc', {})(<SelectAjax service={this.getNamhoc} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Học kì">
              {getFieldDecorator('hocki', {})(<SelectAjax service={this.getSemester} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Lớp">
              {getFieldDecorator('lop', {})(<SelectAjax service={this.getLop} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Hình thức thu">
              {getFieldDecorator('hinhthucthu', {})(<SelectAjax service={this.getHinhthucthu} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Loại thu">
              {getFieldDecorator('loaithu', {})(<SelectAjax service={this.getLoaithu} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Lần thu">
              {getFieldDecorator('khoahoc', {})(<SelectAjax service={this.getLanthu} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="ĐT học phí">
              {getFieldDecorator('doituonghocphi', {})(<SelectAjax service={this.getDoituonghocphi} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Người lập">
              {getFieldDecorator('nguoilap', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Số phiếu">
              {getFieldDecorator('sophieu', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Mã SV">
              {getFieldDecorator('masinhvien', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={2} style={{ textAlign: 'center' }}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('phieuhuy', {})(<Checkbox checked={phieuhuy}>Phiếu hủy</Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={2} style={{ textAlign: 'center' }}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('phieuthu', {})(<Checkbox checked={phieuthu}>Phiếu thu</Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={2} style={{ textAlign: 'center' }}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('phieuchi', {})(<Checkbox checked={phieuchi}>Phiếu chi</Checkbox>)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Đến ngày">
              {getFieldDecorator('tungay', {})(<DatePickerString placeholder='Từ ngày' />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Từ ngày">
              {getFieldDecorator('denngay', {})(<DatePickerString placeholder='Đến ngày' />)}
            </Form.Item>
          </Col>
          <Col span={6} style={{ textAlign: 'right' }}>
            <b style={{ marginRight: '20px' }}>Tổng số phiếu:</b><b style={{ color: 'red', fontSize: '15px' }}>7</b>
          </Col>
          <Col span={6} style={{ textAlign: 'right' }}>
            <b style={{ marginRight: '20px' }}>Tổng số tiền:</b><b style={{ color: 'red', fontSize: '15px' }}>20.550.000</b>
          </Col>
        </Row>
        <Row style={{ margin: '10px 0px' }}>
          <Col style={{ float: 'right' }}>
            <div className="btn-group">
              <Btn htmlType="submit" title='Liệt kê phiếu' />
              <BtnClear onClick={this.handleReset} />
            </div>
            <div className="btn-group">
              <Btn title='Xuất Excel' type='primary' disabled onClick={this.exportExcel} />
            </div>
            <div className="btn-group">
              <Btn title='In' />
              <Select defaultValue="1" style={{ minWidth: '300px' }}>
                <Option value="1">Danh sách chi tiết biên lai</Option>
                <Option value="2">Danh sách chi tiết biên lai phải thu</Option>
                <Option value="3">Tổng hợp theo ngày và người thu</Option>
              </Select>
            </div>
          </Col>
        </Row>
      </Form >
    );
  }
}

export default SearchBar;



