import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, message } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import SearchBar from './SearchBar';
import { TableData } from '../../../components/common';
import { Btn } from '../../../components/common/button/index';
import { getAdmissionReceipts } from '../../../services/admissionReceipts';
import { moneyToString } from '../../../utils/formatUtil';

class AdmissionReceipts extends Component {
  state = {
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  columns = [
    {
      title: 'Ký hiệu',
      dataIndex: 'kyhieu',
      key: 'kyhieu',
    },
    {
      title: 'Số phiếu',
      dataIndex: 'sophieu',
      key: 'sophieu',
    },
    {
      title: 'Ngày thu',
      dataIndex: 'ngaythu',
      key: 'ngaythu',
    },
    {
      title: 'Mã SV',
      dataIndex: 'masinhvien',
      key: 'masinhvien',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'hovaten',
      key: 'hovaten',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'tenlop',
      key: 'tenlop',
    },
    {
      title: 'Số tiền',
      dataIndex: 'sotien',
      render: text => moneyToString(text),
      key: 'sotien',
    },
    {
      title: 'Nội dung',
      dataIndex: 'noidung',
      key: 'noidung',
    },
    {
      title: 'Người thu',
      dataIndex: 'nguoithu',
      key: 'nguoithu',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: () => (
        <Fragment>
          <Btn onClick={this.xemphieu}>Xem phiếu</Btn>
          <Btn onClick={this.huyphieu}>Hủy phiếu</Btn>
        </Fragment>
      ),
    },
  ];

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  xemphieu = () => {
    message.success('Bạn vừa nhấn xem phiếu');
  }

  huyphieu = () => {
    message.warning('Bạn vừa nhấn hủy phiếu');
  }

  render() {
    return (
      <Card>
        <Row>
          <Col span={24}>
            <SearchBar />
          </Col>
          <Col span={24}>
            <TableData
              columns={this.columns}
              paramSearch={this.state.paramSearch}
              service={getAdmissionReceipts}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AdmissionReceipts />
  </Page>
);



